<template>
  <div class="home">
    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect" background-color="#545c64"
             text-color="#fff"
             active-text-color="#ffd04b">
      <el-menu-item ><i class="el-icon-s-comment" style="color: #fff"></i></el-menu-item>
      <el-menu-item  class="item2">聊天中心</el-menu-item>
    </el-menu>
    <div class="chatroom">
      <header>
        <h1>多人聊天室</h1>
      </header>
      <article>
        <ul>
          <li v-for="item in msgList" :key="item.time" :class="item.class">
            <div>
              <p :class="item.class+'time'">{{item.time}}</p>
              <div :class="item.class+'user'">
                <p>{{item.user}}</p>
                <p>{{item.content}}</p>
              </div></div>
          </li>
        </ul>
      </article>
      <footer>
        <el-input v-model="inputVal"></el-input>
        <el-button @click="onInpuKeyDown" type="primary">发送</el-button>
      </footer>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import io from 'socket.io-client/dist/socket.io'

export default {
  name: 'Home',
  data(){
    return {
      socket:null,
      msgList:[],
      inputVal:null,
      name:"无名"
    }
  },
  mounted() {
      let name = this.$route.query.name;
      this.name = this.$route.query.name;
      console.log(name);
      let self = this;
      this.socket = io(`ws://127.0.0.1:80?name=${name}`);
      this.socket.on('connect', function(){
        console.log('连上了')
      });
      // 这里接收服务端发来的单独消息
      this.socket.on('singleMsg', function(msg){
        console.log(msg)
        switch (msg.event) {
                // 如来自服务端的问候，虽然这里没写什么，但是可以据此做一些页面上的效果
          case 'greet_from_server':
            self.$message(msg.data)
            break
          default:
            break
        }
      })
      // 目前还没用到，可拓展
      this.socket.on('broadcastAll', function(msg){
        switch (msg.event) {
                // 如来自服务端的问候，虽然这里没写什么，但是可以据此做一些页面上的效果
          case 'new_user_join':
            self.msgList.push({
              time: new Date().toLocaleString(),
              user: '系统通知',
              content: `用户 ${msg.data.user} 加入了聊天室......`,
              class:"systemclass"
            })
            break
          default:
            break
        }
      })
      // 监听广播
      this.socket.on('broadcast', function(msg){
        console.log(msg)
        switch (msg.event) {
                // 新用户加入聊天室
          case 'new_user_join':
            self.msgList.push({
              time: new Date().toLocaleString(),
              user: '系统通知',
              content: `用户 ${msg.data.user} 加入了聊天室......`,
              class:"systemclass"
            })
            break
                // 用户退出聊天室
          case 'someone_exit':
            self.msgList.push({
              time: new Date().toLocaleString(),
              user: '系统通知',
              content: `用户 ${msg.data.user} 退出了聊天室......`,
              class:"systemclass"
            })
            break
                // 接收某用户的聊天内容
          case 'new_chat_content':
            self.msgList.push({
              time: new Date().toLocaleString(),
              user: msg.data.user,
              content: msg.data.content,
              class:msg.data.class
            })
            break
          default:
            break
        }
      })
      this.socket.on('disconnect', function(){
        console.log('连接断开了')
      });

  },
  methods:{
//监听输入框回车事件

    onInpuKeyDown() {


        // 将输入的聊天内容推送给服务端
        this.socket.emit('chat', {name:this.name,value:this.inputVal});
        this.msgList.push({
          time: new Date().toLocaleString(),
          user: this.name,
          content: this.inputVal,
          class:"myclass"
        })
        this.inputVal = '';
    }
  },
  components: {

  },
  destroyed() {

  }
}
</script>
