<template>
    <div>
        <h1>aaa</h1>
        <el-container>
            <el-header>Header</el-header>
            <el-main>
                <el-form ref="form" :model="form" label-width="100px">
                    <el-form-item label="昵称">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="密码">
                        <el-input v-model="form.password"></el-input>
                    </el-form-item>
                    <el-button type="primary" @click="onRegister">立即注册</el-button>
                    <el-button type="primary" @click="onSubmit">立即登录</el-button>
                </el-form>
            </el-main>
        </el-container>
    </div>
</template>

<script>
    import axios from "axios"
    import qs from "qs"
    export default {
        name: "login",
        data() {
            return {
                form: {
                    name: '',
                    password:'',
                    isconnected:false
                },
                socket:null
            }
        },
        created() {

        },
        methods:{
            onSubmit(){
                if(this.form.name == '' || this.form.password == ''){
                    this.$message("昵称或密码不能为空！请重新输入");

                }else{
                    console.log(qs.stringify(this.form, { indices: false }));
                    axios({
                        method:"post",
                        url:"/api/login",
                        data:qs.stringify(this.form, { indices: false })
                    }).then(data=>{
                        if(data.data.islogin){
                            this.$router.push({
                                path:`/home?name=${this.form.name}`
                            });
                        }else{
                            this.$message(data.data.data);
                        }
                    });
                }
            },
            onRegister(){
                if(this.form.name == '' || this.form.password == ''){
                    this.$message("昵称或密码不能为空！请重新输入");

                }else{
                    axios.post("/api/register",qs.stringify(this.form,{indices:false})).then(res=>{
                        if(res.data.islogin){
                            this.$router.push({
                                path:`/home?name=${this.form.name}`
                            });
                        }else{
                            this.$message(res.data.data);
                        }
                    });
                }
            }
        }
    }
</script>

<style scoped>

    .el-header, .el-footer {
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        line-height: 60px;
    }

    .el-aside {
        background-color: #D3DCE6;
        color: #333;
        text-align: center;
        line-height: 200px;
    }

    .el-main {
        background-color: #E9EEF3;
        color: #333;
        text-align: center;
        line-height: 160px;
    }
    .el-form{
        width: 35%;
        margin: 0 auto;
        border: 1px solid #ccc;
        padding: 30px;
        border-radius: 4px;
        box-shadow: 5px 5px 15px 0 rgba(0,0,0,.5);
    }
    .el-button{
        width: 40%;
    }
    .el-input{

    }
</style>